@import url(https://fonts.googleapis.com/css2?family=MonteCarlo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
    min-height: 100vh;
    text-align: center;
    overflow-x: hidden;
    /* background-image: url(../assets/type.jpeg); */
    background-size: cover;
    background-repeat: no-repeat;
    overflow-y: auto;
}

.Home{
    background-color: rgba(135, 207, 235, 0.438);
}

.logo{
    display: flex;
    align-content: center;
    justify-content: center;
    width: 100%;  
    
}

.logo img{
    height: 100px;
    width: 200px;
}


.home-image{
display: flex;
align-content: center;
justify-content: center;
width: 100%;
}

.home-image img{
width: 60%;
height: 500px;
}



.faculty{
    font-size: calc(10px + 1vw);
    margin-top: auto;
}

#school{
    margin: -1.5% auto 1% auto;
    font-size: calc(10px + 0.5vw);
    font-weight: 200;
}

.consultancy{
    text-transform: uppercase;
    font-size: calc(10px + 0.6vw);
  color: rgba(0, 0, 0, 0.712);
  font-weight: 200;
  
    
}
.consultancy span{
    display: flex;
    align-content: center;
    justify-content: center;
    text-transform: capitalize;
    font-size: calc(18px + 1.4vw);
    margin-top: 1px;
    color: rgb(248, 79, 144);    
    font-family: MonteCarlo;
    -webkit-animation: name 5s ease-in-out infinite alternate;
            animation: name 5s ease-in-out infinite alternate;
  font-weight: 600;
}

.by{
margin: -1.5%;
text-transform: uppercase;
font-weight: lighter;
}

.name{
    font-size: calc(15px + 1.4vw);
    color: rgb(248, 79, 144);
    font-family: MonteCarlo;
    -webkit-animation: name 5s ease-in-out infinite alternate;
            animation: name 5s ease-in-out infinite alternate;
    font-weight: 600;
    margin-bottom: -2px;
}

.details{
    margin-top: -1%;
    margin-bottom: 1%;
}

.id-number{
    font-size: calc(8px + 0.7vw);
    color: rgba(0, 0, 0, 0.6);
}
.id-number span{
    color: black;
    font-weight: 600;
    font-size: calc(8px + 1vw);
}

.course{
    font-weight: 400;
    font-size: calc(10px + 0.5vw);
    justify-content: center;
    align-content: center;
    text-transform: uppercase;
    color: black;
    /* margin-bottom: -1%; */
}


.tutor{
    font-size: calc(8px + 0.7vw);
    font-weight: 300;
    color: rgba(0, 0, 0, 0.8);
}

.tutor span{
    font-weight: 600;
    font-size: calc(9px + 0.7vw);
}




.Header{
    text-align: left;  
}

.page-title{
    font-size: calc(10px + 0.8vw);
    font-weight: 600;
    text-decoration: underline;
    margin: 2% 0 -1% 0;
    color: rgb(248, 79, 144);    
}
.footer{
    position: static;
    bottom: 0;
    left: 0;
    width: 100%;
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;    
    color: rgba(161, 172, 187, 0.363);
    text-transform: capitalize;
    font-weight: 600;
    margin: -2.5% auto 1% 0;
    cursor: text;
}

.scroll-div{
    display: flex;
    align-content: center;
    justify-content: center;
    right: 0.5%;
    height: 100vh;
    position: fixed;
    margin-top: -3%;
    flex-direction: column;
    

}

.scrollbar{
    opacity: 0.1;
    cursor: pointer;
    padding-left: 5%;
}

.scroll-div :hover{
    color: rgb(43, 9, 9);
 opacity: 0.6;
}

.image{
    width: auto;
    height: 100%;
    padding: 2.5% 0;
    display: flex;
    align-content: center;
    justify-content: center;
   
}

img{
    height: 400px;
    width: 60%;
    /* display: flex; */

}
body{
  overflow: hidden;
  min-height: 100vh;
  scroll-behavior: smooth;
}

.buttons{
display: flex;
flex-direction: row;
width: 100%;
justify-content: space-between;
padding-bottom: 1.5%;
 }

 .home-button{
    display: flex;
    margin-top: 1.5%;
    margin-left: 5%;
    margin-bottom: 1.5%;
    justify-content: right;

}

.article{
  margin: -1% 5%;
  text-align: justify;
  font-size: calc(9px + 0.5vw);
  line-height: calc(9px + 0.5vw + 14px);
  word-spacing: 5px;
}

.next-btn{
display: flex;
 margin-top: 1.5%;
 margin-right: 5%;
 margin-bottom: 1.5%;
 justify-content: right;
}



.ref{
color: rgb(28, 92, 211);
margin: auto;
pointer-events: all;
word-spacing: 10px;
}

.reference-list{
  font-size: 13px;
  /* width: 100%; */
}


button{
  text-decoration: none !important;
}

.page2-images{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  /* width: 100%; */
  align-items: center;
  margin: 1%;
}

.extra-images{
  width: 480px;
  margin: 2px;
  display: flex;
  justify-content: center;
  align-content: center;
}

